.category-content{
    .category-content-container{

        &>p:first-child{
            font-size:1.5rem;
            font-weight:500;
            padding-bottom:$base-spacing-unit;
            border-bottom:$border-dashed;
            @include tablet{
                font-size: 4.5vmin;
            }
        }

       &>p{
            font-size:1rem;
            margin: $base-spacing-unit 0;
            @include tablet{
                font-size: 3.75vmin;
                font-weight:500;
                margin: 1em 0;
            }
        }

        h2{
            padding-bottom:$base-spacing-unit;
            border-bottom:$border-dashed;
            @include tablet{
                font-size: 2.5vmin;
                font-weight: 500;
                padding-bottom: 3rem;   
            }
        }

        h2:last-of-type{
            margin-bottom: $base-spacing-unit;
            @include tablet{
                margin-bottom: 4.5rem;
            }
        }

        nav.number-pagination{

            &>*{
                padding-right: 0.5em;
                font-size:0.75rem;
                font-weight:500;
                @include tablet{
                    font-size: 2.5vmin;
                }
            }
        }
    }
}